nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  height: 10vh;
  padding: 10px;
  transition: all 0.2s ease-in-out;
  position: fixed;
  background-color: $black;
  z-index: 99999;
  opacity: 1;

  h1 {
    color: $red;
    font-family: $logoFont, sans-serif;
    font-size: calc(2rem + 2.5vw);
    user-select: none;
    cursor: pointer;
  }

  ul {
    display: flex;
    align-items: center;
    gap: 3vw;

    li {
      font-size: 22px;
    }
  }

  .search_bar {
    display: flex;
    align-items: center;
    border-bottom: 2px solid $white;
    padding: 2px;
    border: 2px solid $white;
    border-radius: 5px;
    transition: all 0.2s;

    &:hover {
      border: 2px solid $red;
    }
    &:active {
      border: 2px solid $red;
    }

    input {
      outline: none;
      padding: 1px;
      background: none;
      border: none;
      color: $white;

      &::-webkit-search-cancel-button {
        display: none;
      }

      &::-ms-clear {
        display: none;
      }
    }
  }

  .icon {
    height: 25px;
    cursor: pointer;
    margin-right: 5px;
  }

  .active {
    position: relative;
  }

  .active::after {
    content: "";
    display: block;
    width: 0%;
    height: 2.5px;
    border-radius: 5px;
    background-color: $red;
    position: absolute;
    bottom: -4px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-animation: anim 0.3s ease forwards;
    animation: anim 0.3s ease forwards;
  }

  @-webkit-keyframes anim {
    to {
      width: 25%;
    }
  }
  @keyframes anim {
    to {
      width: 25%;
    }
  }

  .menu {
    display: none;
  }

  @media screen and (max-width: 815px) {
    .menu {
      display: block;
      position: sticky;
      z-index: 200;
      cursor: pointer;
    }

    ul {
      flex-direction: column;
      position: absolute;
      right: -200%;
      top: 0%;
      gap: 100px;
      background-color: $black;
      width: 42vw;
      height: 100vh;
      z-index: 1;
      padding-top: 200px;
      transition: all 0.2s;
    }

    .n-visible {
      right: 2%;
    }

    .n-hidden {
      right: -200%;
    }
  }
}

@media screen and (max-width: 509px) {
  .menu {
    display: block;
    position: sticky;
    z-index: 200;
    cursor: pointer;
  }

  .n-visible {
    right: 2%;
    width: 60vw;
  }

  .n-hidden {
    right: -200%;
  }
}
