.card-container {
  margin-top: 10vh;

  .card {
    // background-color: blue;
    width: 267px;
    height: 470px;
    margin: 2px 5px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
  }

  .movie-img {
    height: 400px;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      transform: scale(1.04);
      z-index: 1;
    }
  }
}
