@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@700&display=swap");

// Fonts

$font1: "Roboto";
$logoFont: "Yanone Kaffeesatz";

// Colors
$black: #000;
$red: rgb(236, 28, 28);
$white: #fff;

// :export {

// }

* {
  padding: 0;
  margin: 0;
  font-family: $font1, sans-serif;
}

html {
  scroll-behavior: smooth;
}

*::selection {
  background-color: $red;
}

body {
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  background: linear-gradient(176deg, $black 50%, #1f1f1f 100%);
  background-attachment: fixed;
  color: $white;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: $white;
}
